import connectionDb from "../../../lib/db";
import { NextResponse } from "next/server";
import User from "../../../models/User";
import bcrypt from "bcryptjs";

export async function POST(request) {
  let body = null;

  // STEP 1: Safely parse JSON
  try {
    body = await request.json();
  } catch (jsonError) {
    console.error("❌ JSON Parsing Error:", jsonError);

    return NextResponse.json(
      { success: false, message: "Invalid JSON format" },
      { status: 400 }
    );
  }

  const { name, phone, role, password } = body || {};

  // STEP 2: Validate required fields
  if (!name || !phone || !role || !password) {
    console.error("❌ Validation Error: Missing required fields", body);

    return NextResponse.json(
      { success: false, message: "All fields are required" },
      { status: 400 }
    );
  }

  if (password.length < 6) {
    console.error("❌ Password too short:", password);

    return NextResponse.json(
      { success: false, message: "Password must be at least 6 characters" },
      { status: 400 }
    );
  }

  if (phone.length !== 11) {
    console.error("❌ Incorrect phone length:", phone);

    return NextResponse.json(
      { success: false, message: "Phone number must be 11 digits" },
      { status: 400 }
    );
  }

  // STEP 3: Connect to database
  try {
    await connectionDb();
  } catch (dbError) {
    console.error("❌ Database Connection Error:", dbError);

    return NextResponse.json(
      { success: false, message: "Database connection failed" },
      { status: 500 }
    );
  }

  // STEP 4: Check if user already exists
  let existingUser;
  try {
    existingUser = await User.findOne({ phone });
  } catch (findError) {
    console.error("❌ User Find Error:", findError);

    return NextResponse.json(
      { success: false, message: "Error checking existing user" },
      { status: 500 }
    );
  }

  if (existingUser) {
    console.error("❌ User already exists:", existingUser.phone);

    return NextResponse.json(
      { success: false, message: "User already exists" },
      { status: 400 }
    );
  }

  // STEP 5: Safely hash password
  let hashedPassword;
  try {
    hashedPassword = await bcrypt.hash(password, 10);
  } catch (hashError) {
    console.error("❌ Error hashing password:", hashError);

    return NextResponse.json(
      { success: false, message: "Error securing password" },
      { status: 500 }
    );
  }

  // STEP 6: Create and save user
  try {
    const newUser = new User({
      name,
      phone,
      role,
      password: hashedPassword,
    });

    await newUser.save();

    console.log("✅ New User Registered Successfully:", {
      name,
      phone,
      role,
      registeredAt: new Date().toISOString(),
    });

    return NextResponse.json(
      { success: true, message: "User created successfully" },
      { status: 201 }
    );
  } catch (saveError) {
    console.error("❌ User Save Error:", saveError);

    return NextResponse.json(
      { success: false, message: "Failed to save user" },
      { status: 500 }
    );
  }
}
